import React, { useState, useEffect, Fragment } from "react"
import { Grid, Typography } from "@mui/material";
import LoginButton from "../login";
import LogoutButton from "../logout";
import Profile from "../profile";
import Link from "@mui/material/Link";
import {Link as RouterLink} from "react-router-dom";

function Home() {
  // constants
  let URL_BASE = ""
  if (process.env.NODE_ENV === 'development') {
    URL_BASE = "http://localhost:8080/storylist"
  } else {
    URL_BASE = "https://gameserver-edjui4r2ua-ew.a.run.app/storylist"
  }

  const [stories, setStories] = useState([])

  useEffect(() => {
    getStoryList();
    // eslint-disable-next-line -- empty [] intentional to make this RUNONCE
  }, [])

  return (
    <Grid container sx={{maxWidth: 4/5}}>
      <Grid item xs={12}>
        <Typography variant="h1">Home</Typography>
      </Grid>
      <Grid item xs={12}>
        <Typography variant="body1" sx={{mb: 2}}>Welcome to the Gameserver test UI.
        This is a simple React framework (using MUI for layout) for displaying
        the responses from the FastAPI server. It's not intended for production
        use but it's faster to prototype than the Flutter UI (until Young
        teaches me Flutter ;)
        </Typography>
        <Typography variant="body1" sx={{mb: 2}}>
        Use the menu in the upper-left to choose a module.
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <ul>{stories.map((story, index) => {
        console.log(story.title);
        return (
        <li>
          <Link
            component={RouterLink}
            key={index}
            to={"/story/" + story.content_id}>{story.title}
          </Link> ({story.timestamp})
        </li>
        )
        })}
        </ul>
      </Grid>
      <Grid item xs={12}>
        <LoginButton></LoginButton> <LogoutButton></LogoutButton>
      </Grid>
      <Grid item xs={12}><Profile>Name</Profile></Grid>
    </Grid>
  )

  // fetch list of stories
  function getStoryList() {
    let storyURL = `${URL_BASE}/`
    console.log(`Fetching storylist from ${storyURL}`);
    fetch(storyURL)
      .then(response => {
        console.log(`${storyURL} response.status = ${response.status}`)
        if (response.ok) {
          return response.json()
        } else {
          throw new Error("Status code: " + response.status);
        }
      })
      .then(data => {
        // Process the retrieved data
        console.log("Fetched list... processing");
        console.log(data)
        setStories(data)
      })
      .catch(error => {
        // Handle any errors
        console.error(`Could not retrieve story from ${storyURL}; ${error}`)
      });
  }
};


export default Home;
