import React from "react"
import { Routes, Route, HashRouter } from 'react-router-dom';
import Nav from "./pages/nav";
import Home from "./pages/home";
import Story from "./pages/story";
import StoryChain from "./pages/storychain";
import Itinerary from './pages/itinerary';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { Grid } from "@mui/material";

import './App.css';

const darkTheme = createTheme({
  palette: {
    mode: 'dark',
  },
  typography: {
    body1: {
      marginBottom: 2,
    }
  },
})

// constants
let GAMESERVER_BACKEND_URL_BASE = ""
if (process.env.NODE_ENV === 'development') {
  GAMESERVER_BACKEND_URL_BASE = "http://localhost:8080/"
} else {
  GAMESERVER_BACKEND_URL_BASE = "https://gameserver-edjui4r2ua-ew.a.run.app/"
}


function App() {
  // TODO(risher): Change HashRouter to BrowserRouter when not deploying to Github Pages
  return (
    <div className="App">
      <ThemeProvider theme={darkTheme}>
        <CssBaseline />
        <Grid container>
          <HashRouter>
            <Routes>
              <Route path="*" element={<Nav />}>
                <Route index element={<Home />} />
                <Route path="itinerary" element={<Itinerary />} />
                <Route path="story">
                  <Route path=":contentId?" element={<Story />} />
                </Route>
                <Route path="storychain">
                  <Route path=":contentId?" element={<StoryChain />} />
                </Route>
              </Route>
            </Routes>
          </HashRouter>
        </Grid>
      </ThemeProvider>
    </div>
  );
}

export default App;
